.home__page {
    position: relative;
    max-width: 1110px;
    margin: 0 auto;

    .page {
        &__jobBoard {
            // display: flex;
            display: grid;
            grid-template-columns: repeat(3, 1fr);
            grid-column-gap: 30px;
            flex-wrap: wrap;
            margin-top: 4.5rem;
            // justify-content: space-between;
        }
    }
}

// ==  Passage mode Tablette
@media screen and (max-width : 1111px) {
    .home__page {
        max-width: 689px;

    }
    .page__jobBoard {
        margin-top: 3rem  !important;
        grid-template-columns:1fr 1fr !important;
    }
}

// == Passage mode mobile
@media screen and (max-width : 683px) {
    .page__jobBoard {
        flex-direction: column;
        align-items: center;
    }
}

